@import url('https://fonts.googleapis.com/css2?family=Dancing+Script:wght@400..700&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Indie+Flower&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Italianno&display=swap');

.presentes-main {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin-bottom: 40px;
    font-family: "Italianno", cursive;
    font-size: 30px;
    font-weight: 400;
    font-style: normal;
    color: #182062;
}
.opcao1, .opcao2 {
    width: 80%;
    display: flex;
    flex-direction:column;
}

.pix-email {
    text-align: center;
}

.pix-info {
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: center;

}

.pix-info-text {
    width: 30%;
    justify-content: center;
    align-items: center;
    text-align: center;
}

.pix-info-img {
    width: 30%;
    align-items: center;
    justify-content: center;
}

#nubank-img {
    width: 200px;
    height: auto;
    border-radius: 10px;
}

#pix-email {
    font-family: "Dancing Script", cursive;
    text-align: center;
    font-size: 30px;
    font-weight: 400;
    font-style: normal;
    color: #182062;
}

#pix-extra {
    font-family: "Indie Flower", cursive;
    font-size: 15px;
    font-weight: 600;
    font-style: normal;
    text-align: center;
}

.espaco {
    width: 100%;
    height: 70px;
}

.icasei-img-main {
    width: 100%;
    display: flex;
    justify-content: center;
}

.icasei-img-link {
    width: 130px;
}

#icasei-img {
    width: 100%;
    padding-top: 10px;
}

@media(max-width:440px){
    .opcao1, .opcao2 {
        text-align: justify;
    }

    .pix-info {
        flex-direction: column;
    }

    .pix-info-text {
        width: 95%;
    }

    .pix-info-img {
        width: 95%;
        display: flex ;
        justify-content: space-around;
        align-items: center;    
    }
}