@import url('https://fonts.googleapis.com/css2?family=Italianno&display=swap');

.nav-header {
    display: flex;
    justify-content: center;
    background-color: #f8f6f0;
}

ul {
    display: flex;
    width: 800px;
    justify-content: space-between;
}

li {
    list-style: none;
    font-family: "Italianno", cursive;
    font-size: 30px;
    font-weight: 400;
    font-style: normal;
    color: #182062;
    cursor: pointer;
}

.icon {
    color: #182062;
}

.nav-header-mobile {
    display: none;
}


@media(max-width:600px){
    .nav-header {
        display: none;
    }
    
    ul {
        width: 80%;
    }

    .nav-header-mobile {
        width: 100%;
        display: flex;
        justify-content:space-between;
    }
}
